.wp-block-iowa-aea-theme-side-tabs {
	background-color: transparent;
	color: #fff;
	padding: 2px;
	display: flex;
	position: relative;
}

.impact-tab{
	background: var(--tab-background, white);
	border-radius: 0 19px 19px 0;
	padding: 3rem 2rem 3rem 3rem;
	position: relative;
	min-height: 350px;

	&.open {
		// Styles for open tab
		width: 100%;
		* {
			width: 100%;
			display: block;
		}
	}

	width: 2rem;
	overflow: hidden;
	* {
		width: 0;
		overflow: hidden;
		display: none;
	}

	&:nth-child(1) {
		z-index: 4;
	}

	&:nth-child(2) {
		margin-left: -2rem;
		z-index: 3
	}

	&:nth-child(3) {
		margin-left: -2rem;
		z-index: 2;
	}

	&:nth-child(4) {
		margin-left: -2rem;
		z-index: 1;
	}



	h3 {
		color: white;
	}
	color: white;

	&:before {
		--font-size: 1.5rem;
		content: attr(data-tab-num);
		display: grid;
		place-items: center;
		border: 2px solid white;
		border-radius: 50%;
		height: calc(1.5 * var(--font-size));
		width: calc(1.5 * var(--font-size));
		margin-right: 1rem;
		font-size: var(--font-size);
		text-align: center;
		line-height: 0;
	}
}
